'use client'

import React, { useEffect, useState } from 'react'
import PageHeading from '@/components/PageHeading'
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table'
import { Button } from '@/components/ui/button'
import { Card } from '@/components/ui/card'
import apiClient from '@/lib/AxiosUtils'
import Loader from '@/app/loading'

const UsersPage = () => {
  const [users, setUsers] = useState<any[]>([])
  const [loading, setLoading] = useState(false)
  const [page, setPage] = useState(0)
  const [totalPages, setTotalPages] = useState(1)

  const fetchUsers = async (currentPage: number) => {
    setLoading(true)
    try {
      const res = await apiClient.get(`/user/all-users`, {
        params: {
          page: currentPage,
          size: 10,
          sortBy: 'userRegDate',
          sortDir: 'desc',
          type: 'ALL',
        },
      })
      setUsers(res.data.result || [])
      setTotalPages(res.data.pagination?.totalPages || 1)
    } catch (error) {
      console.error('Error fetching users:', error)
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    fetchUsers(page)
  }, [page])

  return (
    <div className='p-6'>
      <PageHeading title='All Users' />
      <Card className='mt-6 p-4'>
        {loading ? (
          <div className='text-center py-6 text-sm text-muted-foreground'><Loader /></div>
        ) : users.length === 0 ? (
          <div className='text-center py-6 text-sm text-muted-foreground'>No users found.</div>
        ) : (
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>User ID</TableHead>
                <TableHead>Email</TableHead>
                <TableHead>Mobile</TableHead>
                <TableHead>Roles</TableHead>
                <TableHead>Status</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {users.map((user) => (
                <TableRow key={user.userId}>
                  <TableCell>{user.userId}</TableCell>
                  <TableCell>{user.userEmail}</TableCell>
                  <TableCell>{user.mobileNo}</TableCell>
                  <TableCell>
                    {user.userRoles?.map((ur: any) => ur.roles?.roleTitle).join(', ')}
                  </TableCell>
                  <TableCell>
                    <span className={user.enabled ? 'text-green-600' : 'text-red-600'}>
                      {user.enabled ? 'Enabled' : 'Disabled'}
                    </span>{' '}
                    |{' '}
                    <span className={user.deleted ? 'text-red-500' : 'text-green-500'}>
                      {user.deleted ? 'Deleted' : 'Active'}
                    </span>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        )}

        {/* Pagination Controls */}
        <div className='flex items-center justify-between mt-4'>
          <Button
            variant='outline'
            disabled={page === 0}
            onClick={() => setPage((prev) => Math.max(prev - 1, 0))}
          >
            Previous
          </Button>
          <span className='text-sm text-muted-foreground'>
            Page {page + 1} of {totalPages}
          </span>
          <Button
            variant='outline'
            disabled={page + 1 >= totalPages}
            onClick={() => setPage((prev) => Math.min(prev + 1, totalPages - 1))}
          >
            Next
          </Button>
        </div>
      </Card>
    </div>
  )
}

export default UsersPage
